.prayers {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    margin-top: 3%;
    margin-bottom: 7%;
}

.prayers .prayers-titles {
    font-weight: 900;
}

.prayers .prayers-titles,
.prayers .prayers-list {
    background-color: darkblue;
    padding: 1.6% 3%;
    font-size: 22px;
    width: 80%;
    margin: auto;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    column-gap: 6%;
}

.prayers .prayers-titles li {
    border-bottom: 1.7px solid #fff;
    text-align: center;
}

.prayers .prayers-list li {
    text-align: center;
    font-weight: 700;
}