.cities {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    margin-bottom: 3%;
}

.cities .cities-dropdown {
    outline: none;
    border: 1px solid white;
    width: 20%;
    text-align: center;
    padding: 5px;
    border-radius: 7px;
    font-size: 16px;
    font-weight: 700;
    color: darkblue;
    cursor: pointer;
}