.footer {
    margin-top: auto;
    padding: 16px;
    background-color: darkblue;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    justify-content: center;
}

.footer-inner {
    margin-top: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.footer-inner .footer-inner-left,
.footer-inner .footer-inner-right {
    width: 60%;
    display: flex;
    flex-direction: column;
    margin: 0 10%;
    gap: 10px;
}

.footer-inner .footer-inner-left p,
.footer-inner .footer-inner-right p {
    font-size: 20px;
    font-weight: 700;
}